.container{
    display: flex;
    flex-direction: column;
    gap: 10px;
}

/* DESCKTOP */
  @media screen and (min-width: 1201px) {
    .container{
        display:grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        gap: 10px;
    }
    
  }
  
  